body {
  margin: 20;
  padding: 20;
}

h1 {
  color: green;
  font-style: italic;
}

.note {
  color: grey;
  padding-top: 5px;
  font-size: 15px;
}

.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.notes {
  margin-top: 10px;
  margin-left: 3px;
}

.wrapper {
  margin-top: 10px;
  padding-top: 5px;
  padding-left: 2px;
  display: grid;
  grid-template-columns: 800px 150px;
  border-style: solid;
  border-width: 1px;
}



